header {
  background-color: #82a6cb;
  grid-template-columns: 2fr repeat(8, 1fr) 2fr;
  place-content: center;
  place-items: center;
  gap: 10px;
  min-width: 700px;
  padding: 10px;
  display: grid;
}

.titulo {
  font-size: 50px;
}

footer {
  background-color: #82a6cb;
  flex-grow: 3;
  grid-template-columns: repeat(9, 1fr) 2fr;
  gap: 10px;
  width: 100%;
  min-width: 700px;
  min-height: 50px;
  max-height: 50px;
  display: grid;
  position: fixed;
  bottom: 0;
}

.contacto {
  grid-column: 10;
  justify-content: end;
  justify-items: center;
}

html, body {
  background-color: #bdd8f1;
  flex-direction: column;
  min-width: fit-content;
  margin: 0;
  display: flex;
}

a {
  color: #000;
  font-size: 32px;
  text-decoration: none;
}

p {
  color: #000;
  margin: 0;
  font-size: 32px;
  text-decoration: none;
}

main {
  grid-template-rows: repeat(11, 60px);
  grid-template-columns: repeat(12, 1fr);
  gap: 10px;
  margin: 0 auto;
  padding: 10px;
  display: grid;
}

.iniciar_sesion {
  grid-column: 10;
  justify-content: end;
  justify-items: center;
}

div.crear_cuenta {
  background-color: #82a6cb;
  border-radius: 30px;
  grid-area: 2 / 4 / 11 / 10;
  grid-template-rows: repeat(14, 1fr);
  grid-template-columns: repeat(6, 1fr);
  place-content: center;
  place-items: center;
  min-width: 700px;
  min-height: 100%;
  display: grid;
}

div p.crear_cuenta {
  grid-area: 2 / 2 / 4 / 6;
  font-size: 48px;
}

p.text_email {
  width: 100%;
  font-size: 32px;
}

label.email {
  grid-area: 5 / 2 / 6 / 6;
  width: 100%;
  height: 100%;
}

input.email {
  width: 100%;
  height: 30%;
}

p.usuario {
  width: 100%;
  font-size: 32px;
}

label.usuario {
  grid-area: 7 / 2 / 8 / 6;
  width: 100%;
  height: 100%;
}

input.usuario {
  width: 100%;
  height: 30%;
}

div p.contrasenia {
  grid-area: 8 / 2 / 9 / 3;
  font-size: 32px;
}

label.contrasenia {
  grid-area: 9 / 2 / 10 / 6;
  width: 100%;
  height: 100%;
}

input.contrasenia {
  grid-area: 9 / 2 / 10 / 6;
  width: 100%;
  height: 30%;
}

label.terminos {
  grid-area: 11 / 2 / 11 / 6;
  height: 100%;
  display: inline-flex;
}

input.terminos {
  height: 50%;
}

button {
  color: #fff;
  background-color: #3667a6;
  border: none;
  grid-area: 12 / 3 / 13 / 5;
  width: 100%;
  font-size: 24px;
}

p.terminos {
  width: 100%;
  font-size: 20px;
}

a.crear_cuenta {
  text-align: center;
  grid-area: 13 / 3 / 14 / 5;
  width: 100%;
  font-size: 24px;
}

@media only screen and (width <= 550px) {
  input {
    font-size: 24px;
  }

  main {
    justify-content: center;
    align-items: center;
    gap: 10px;
    min-width: 700px;
    height: 800px;
    margin-bottom: 7%;
    padding: 10px;
    display: flex;
  }

  div.crear_cuenta {
    background-color: #82a6cb;
    border-radius: 30px;
    grid-template-rows: repeat(14, 1fr);
    grid-template-columns: repeat(6, 1fr);
    display: grid;
    position: absolute;
    top: 20%;
  }

  div p.crear_cuenta {
    grid-area: 2 / 2 / 4 / 6;
    font-size: 64px;
  }

  div p.text_email {
    grid-area: 4 / 2 / 5 / 4;
    width: 100%;
    font-size: 40px;
  }

  input.email {
    grid-area: 5 / 2 / 6 / 6;
    width: 100%;
    height: 50%;
  }

  div p.usuario {
    grid-area: 6 / 2 / 7 / 3;
    width: 100%;
    font-size: 40px;
  }

  input.usuario {
    grid-area: 7 / 2 / 8 / 6;
    width: 100%;
    height: 50%;
  }

  div p.contrasenia {
    grid-area: 8 / 2 / 9 / 4;
    width: 100%;
    font-size: 40px;
  }

  input.contrasenia {
    grid-area: 9 / 2 / 10 / 6;
    width: 100%;
    height: 50%;
  }

  textarea.mensaje {
    grid-area: 8 / 2 / 9 / 6;
    width: 100%;
    height: 100%;
  }

  button {
    color: #fff;
    background-color: #3667a6;
    border: none;
    grid-area: 12 / 3 / 13 / 5;
    width: 100%;
    font-size: 24px;
  }
}
